      <span class="topTitle-span1">岗位人员</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form ref="formValidate" :model="formValidate" :rules="ruleValidate11" :label-width="120" inline>
            <Row>
              <FormItem label="姓名：" >
                <Input v-model="formValidate.val1" placeholder="请输入姓名" style="width:200px"></Input>
