                  <Option
                    :value="item.code"
                    v-for="(item,index) in PROCESS_POST_TYPE"
                    :key="index"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
              <FormItem label="是否从事可疑交易：">
                <Select v-model="formValidate.val5" filterable style="width:200px" placeholder="请选择是否从事可疑交易">
                  <Option value>请选择</Option>
                  <Option value="true">是</Option>
                  <Option value="false">否</Option>
                </Select>
              </FormItem>
              <FormItem label="状态：">
                <Select v-model="formValidate.val7" filterable style="width:200px" placeholder="请选择状态">
                  <Option value>请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item,index) in PROCESS_CHECK_STATE"
                    :key="index"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
            </Row>
            <Row>
              <FormItem label="管理部门：" prop="extraUnit">
                <treeselect
                  v-model="formValidate.val8"
                  valueConsistsOf="ALL" 
              :limit="3"
                  :multiple="true"
                  :options="data1"
                  placeholder="请选择管理部门"
                  :max-height="100"
                  style="width:200px"
                />
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="handleSubmit('formValidate','0')">查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
