                    <td>
                    <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.title"
                  >
                    <p slot="reference" class="spanBox textOverflow disBlock textMaxWidth" @click="watchTableCellFun(item,item.busId,item.id)">{{item.title}}</p>
                  </el-popover>
                  </td>
                  <td>{{item.name}}</td>
                 
                  <td>
                    <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.duty"
