                  <td>{{item.email}}</td>
                 
                  <td>
                    <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.qualification"
                  >
                    <p slot="reference" class=" textOverflow disBlock textMaxWidth" >{{item.qualification}}</p>
                  </el-popover>
                  </td>
                  <td>{{item.workBankYears}}</td>
                  <td>{{item.workAntiYears}}</td>
                  <td>{{item.postType}}</td>
                  <td>{{item.ifSuspect?'是':'否'}}</td>
                  <td class="spanBox"
                    @click="showProcess(item)">{{item.flag}}</td>
                  <td>{{item.ifShow?'是':'否'}}</td>
                  <td class="spanBox" @click="watchFileFun(item.busId,item.id,item)">查看</td>
                 
                   <td>
                    <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.manager"
                  >
                    <p slot="reference" class=" textOverflow disBlock textMaxWidth" >{{item.manager}}</p>
                  </el-popover>
                  </td>
                  <td class="spanBox" @click="watchAppendFun(item,item.busId,item.id)">查看</td>
                  <td>{{item.pubTime}}</td>
                  <td>{{item.updater}}</td>
                  <td>{{item.updateTime}}</td>
                  <td>{{item.updater}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
