          <FormItem label="任务名称" prop="bpmTitle">
            <Input v-model="formValidateModel1.bpmTitle" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="项目" prop="val1">
            <Input v-model="formValidateModel1.val1" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="姓名" prop="val2">
            <Input v-model="formValidateModel1.val2" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="职务" prop="val3">
            <Input v-model="formValidateModel1.val3" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="办公电话" prop="val4">
            <Input v-model="formValidateModel1.val4" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="移动手机号" prop="val5">
            <Input v-model="formValidateModel1.val5" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="邮箱地址" prop="val6">
            <Input v-model="formValidateModel1.val6" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="反洗钱专业资质" prop>
            <Input v-model="formValidateModel1.val7" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="从事金融工作时间（年）" prop="val8">
            <Input v-model="formValidateModel1.val8" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="从事反洗钱工作时间（年）" prop="val9">
            <Input v-model="formValidateModel1.val9" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="岗位类型" prop="val10">
            <RadioGroup v-model="formValidateModel1.val10">
              <Radio
                v-for="(item,index) in PROCESS_POST_TYPE"
                :key="index"
                :label="item.code"
                :disabled="addFlag"
              >{{item.name}}</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="是否从事可疑交易分析" prop="val12">
            <RadioGroup v-model="formValidateModel1.val12">
              <Radio label="true" :disabled="this.addFlag">是</Radio>
              <Radio label="false" :disabled="this.addFlag">否</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="是否公开" prop>
            <i-switch v-model="formValidateModel1.val13" size="large" :disabled="this.addFlag">
              <span slot="true">On</span>
              <span slot="false">Off</span>
            </i-switch>
          </FormItem>
          <FormItem label="编辑内容" prop="val14">
            <Input
              v-model="formValidateModel1.val14"
              type="textarea"
              :autosize="{minRows: 2,maxRows: 5}"
              placeholder="请输入"
              :disabled="this.addFlag"
            ></Input>
          </FormItem>

          <FormItem label="上传附件">
            <showUsualFiles
              :file-obj="dataObj"
              :key="fileId"
              @requestUploadList="receiveUploadList"
            ></showUsualFiles>
          </FormItem>
          <Row v-if="addFlagShow">
            <Row style="text-again:left;border-bottom:1px dashed #E8EAEC;margin-bottom:20px">追加信息</Row>
            <FormItem label="上传附件">
              <showUsualFiles
                :file-obj="dataObj2"
                :key="fileId2"
                @requestUploadList="receiveUploadList2"
              ></showUsualFiles>
            </FormItem>
            <FormItem label="追加内容">
