            <p slot="header" style="color:#f60;text-align:center">
              <span>审核人信息</span>
            </p>
            <div style="text-align:left;overflow-y:auto;height:300px">
              <!-- <FormItem prop="nextStepName" :label-width="10">
                <Input
                  type="textarea"
                  :autosize="{minRows: 2,maxRows: 5}"
                  v-model="formValidateModel1.nextStepName"
                  placeholder="请输入此次审核名称"
                  @on-blur="nextStepNameFun"
                ></Input>
              </FormItem>-->
              <FormItem prop="nextUser" :label-width="10">
                <Tree
                  ref="treeRef"
                  :data="data2"
                  show-checkbox
                  multiple
                  @on-check-change="updateNextUser"
                ></Tree>
              </FormItem>
            </div>
            <div slot="footer">
              <Button
                type="primary"
                size="large"
                long
                @click="handleSubmitLast('formValidateModel1')"
              >立即申请</Button>
            </div>
          </Modal>
        </Form>
      </div>
      <div slot="footer" class="flexCenter">
        <div class="button-content">
          <Button type="primary" @click="save1('formValidateModel1')">{{buttonVal}}</Button>
          <Button @click="modal1=false">取消</Button>
        </div>
      </div>
    </Modal>
    <!-- ============== -->
    <Modal :mask-closable="false"   v-model="watchFileModel" width="90%" title="查看">
      <showUsualFiles :file-obj="dataObj3" :key="fileId3" @requestUploadList="receiveUploadList3"></showUsualFiles>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ============== -->
    <Modal :mask-closable="false"   v-model="watchTableCellDitalModel" width="90%" title="查看">
      <div class="content">
        <Form :label-width="120">
          <FormItem label="项目：">
            <label>{{watchTableCell.val1}}</label>
          </FormItem>
          <FormItem label="姓名：">
            <label>{{watchTableCell.val2}}</label>
          </FormItem>
          <FormItem label="职务：">
            <label>{{watchTableCell.val3}}</label>
          </FormItem>
          <FormItem label="办公电话：">
            <label>{{watchTableCell.val4}}</label>
          </FormItem>
          <FormItem label="移动手机号：">
            <label>{{watchTableCell.val5}}</label>
          </FormItem>
          <FormItem label="邮箱地址：">
            <label>{{watchTableCell.val6}}</label>
          </FormItem>
          <FormItem label="反洗钱专业资质：">
            <label>{{watchTableCell.val7}}</label>
          </FormItem>
          <FormItem label="从事金融工作时间（年）：">
            <label>{{watchTableCell.val8}}</label>
          </FormItem>
          <FormItem label="从事反洗钱工作时间（年）：">
            <label>{{watchTableCell.val9}}</label>
          </FormItem>
          <FormItem label="岗位类型：">
            <label>{{watchTableCell.val10}}</label>
          </FormItem>
          <FormItem label="是否从事可疑交易：">
            <label>{{watchTableCell.val12=='true'?'是':'否'}}</label>
          </FormItem>
          <FormItem label="是否公开：">
            <label>{{watchTableCell.val13?'是':'否'}}</label>
          </FormItem>
          <FormItem label="编辑内容">
            <label>
               <el-popover
                        placement="top-start"
                        width="200"
                        trigger="hover"
                        :content="watchTableCell.val14">
                        <span slot="reference" class=" textMaxWidth disBlock textOverflow" >{{watchTableCell.val14}}</span>
                </el-popover>
            </label>
          </FormItem>
          <FormItem label="附件：">
            <showUsualFiles
              :file-obj="dataObj4"
              :key="fileId4"
              @requestUploadList="receiveUploadList4"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加附件：">
            <showUsualFiles
              :file-obj="dataObj5"
              :key="fileId5"
              @requestUploadList="receiveUploadList5"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加内容：">
            <label>
              <el-popover
                        placement="top-start"
                        width="200"
                        trigger="hover"
                        :content="watchTableCell.val11">
                        <span slot="reference" class=" textMaxWidth disBlock textOverflow" >{{watchTableCell.val11}}</span>
                </el-popover>
            </label>
          </FormItem>
        </Form>
      </div>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ============== -->
    <Modal :mask-closable="false"   v-model="watchAppenModel" width="99%" title="查看">
      <div class="content">
        <Row>
          <table class="tableBox">
            <thead>
              <tr>
                <td v-for="(item,index) in tableHeaderAppend">{{item.name}}</td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item,index) in tableBodyAppend">
                <td>{{index+1}}</td>
                <el-popover
                  placement="top-start"
                  title
                  width="200"
                  trigger="hover"
                  :content="item.content"
                >
                  <td slot="reference" class="textOverflow textMaxWidth textTdTop">{{item.content}}</td>
                </el-popover>
                <td class="spanBox" @click="watchAppenModelAppendFile(item.busId)">查看</td>
                <td>{{item.dept}}</td>
                <td>{{item.createTime}}</td>
                <td>{{item.creator}}</td>
              </tr>
            </tbody>
          </table>
        </Row>
      </div>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ============== -->
    <Modal :mask-closable="false"   v-model="watchAppenModelAppendFileModel" width="80%" title="查看">
      <showUsualFiles :file-obj="dataObj6" :key="fileId6" @requestUploadList="receiveUploadList6"></showUsualFiles>
    </Modal>
    <!-- ===========弹框 -->
    <Modal :mask-closable="false"   v-model="taskPeipleModel2" width="360" class="treeBoxModel">
      <p slot="header" style="color:#f60;text-align:center">
        <span>审核人信息</span>
      </p>
      <div style="text-align:left;overflow-y:auto;height:300px">
        <Form ref="taskPeipleModelData2" :model="taskPeipleModelData2" :rules="ruleValidate2">
          <FormItem prop="nextUser" :label-width="10">
            <Tree
              ref="treeRef2"
              :data="data2"
              show-checkbox
              multiple
              @on-check-change="updateNextUser2"
            ></Tree>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button
          type="primary"
          size="large"
          long
          @click="handleSubmitLast2('taskPeipleModelData2')"
        >立即申请</Button>
      </div>
    </Modal>
    <Modal :mask-closable="false"   v-model="showModal" title="流程详情" class="dialog"  width="80%">
        <processMessage
          :data-obj="dataObj"
          v-if="showModal"
        ></processMessage>
        <div slot="footer"></div>
      </Modal>
    <!-- ========弹框 -->
  </div>
</template>
<script>
import processMessage from "@/components/moneyRiskAssess/common/processMessage"; //过程信息
import showUsualFiles from "@/components/commonComp/showUsualFiles";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect, showUsualFiles ,processMessage},
  created() {
    this.tableList();
    let that = this;
    this.axios
      .post("/system/unit/getExtractUnitAll")
      .then(response => {
        if (response.code == "200") {
          that.selectTreeCleanUp(response.data);
          that.data1 = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/PROCESS_CHECK_STATE")
      .then(response => {
        if (response.code == "200") {
          that.PROCESS_CHECK_STATE = response.data.PROCESS_CHECK_STATE;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/PROCESS_POST_TYPE")
      .then(response => {
        if (response.code == "200") {
          that.PROCESS_POST_TYPE = response.data.PROCESS_POST_TYPE;
        }
      })
      .catch(error => {});
  },
  watch: {
    modal1(a) {
      if (a == false) {
        this.$refs["formValidateModel1"].resetFields();
        this.taskPeipleModel = false;
        this.fileId = !this.fileId;
        this.taskPeipleModel = false;
      }
    }
  },
  data() {
    const validaterNextUser = (rule, value, callback) => {
      if (this.formValidateModel1.nextUser == "" && this.taskPeipleModel) {
        return callback(new Error("请选择一个人"));
      } else {
        callback();
      }
    };
    const validatertaskPeipleModelData2NextUser = (rule, value, callback) => {
      if (this.taskPeipleModelData2.nextUser == "" && this.taskPeipleModel2) {
        return callback(new Error("请选择一个人"));
      } else {
        callback();
      }
    };
    // const validaterNextStepName = (rule, value, callback) => {
    //   if (this.formValidateModel1.nextStepName == "" && this.taskPeipleModel) {
    //     return callback(new Error("请输入名称"));
    //   } else {
    //     callback();
    //   }
    // };
    return {
      showModal:false,
      PROCESS_POST_TYPE: [],
      delateId: "",
      taskPeipleModel2: false,
      taskPeipleModelData2: {
        nextUser: ""
        // nextTitel: ""
      },
      ruleValidate2: {
        // nextTitel: [
        //   {
        //     type: "string",
        //     max: 1000,
        //     message: "您超出了范围",
        //     trigger: "blur"
        //   },
        //   {
        //     required: true,
        //     message: "此项不能为空",
        //     trigger: "blur"
        //   }
        // ],
        nextUser: [
          {
            required: false,
            validator: validatertaskPeipleModelData2NextUser,
            trigger: "on-check-change"
          }
        ]
      },
      tableHeaderAppend: [
        { name: "序" },
        { name: "追加内容" },
        { name: "追加附件" },
        { name: "所属部门" },
        { name: "操作时间" },
        { name: "操作人员" }
      ],
      tableBodyAppend: [
        {
          content: "", //内容
          dept: "", //所属部门
          createTime: "", //创建时间
          creator: "" //创建人
        }
      ],
      watchAppenModelAppendFileModel: false,
      watchAppenModel: false,
      watchTableCell: {
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: "",
        val12: "",
        val13: "",
        val14: ""
      },
      bpmName: "",
      appendId: "",
      appendbusId: "",
      fileId6: true,
      dataObj6: {},
      fileId5: true,
      dataObj5: {},
      fileId4: true,
      dataObj4: {},
      watchTableCellDitalModel: false,
      fileId3: true,
      dataObj3: {},
      watchFileModel: false,
      fileId2: true,
      dataObj2: {},
      buttonVal: "",
      modifyid: "",
      fileId: true,
      dataObj: {},
      uploadList6: [],
      uploadList5: [],
      uploadList4: [],
      uploadList3: [],
      uploadList2: [],
      // ===============
      uploadList: [],
      defaultList: [],
      data2: [],
      taskPeipleModel: false,
      PROCESS_CHECK_STATE: [],
      data1: [],
      ifExacte: "0", //查询方式
      minEnabledTime: "", //时间1
      maxEnabledTime: "", //时间2
      pageSize: 10,
      total: 0,
      pageNum: 1,
      addFlag: false,
      modifyFlag: false,
      modifyFlag: false,
      appendFlag: false,
      addFlagShow: false,
      modifyFlagShow: false,
      // =====================弹框一
      modal1: false,
      modalDisableFlag: true, //修改与新增弹框内容禁用标识
      formValidateModel1: {
        bpmTitle: "",
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: "",
        val12: "",
        val13: "",
        val14: "",
        nextUser: ""
        // nextStepName: ""

        // name: "",
        // val2: "",
        // val3: "", //角色描述
        // val4: "" //说明
      },
      // ======================弹框一
      businessDeptList: [
        {
          value: "New York",
          label: "New York"
        },
        {
          value: "London",
          label: "London"
        },
        {
          value: "Sydney",
          label: "Sydney"
        },
        {
          value: "Ottawa",
          label: "Ottawa"
        },
        {
          value: "Paris",
          label: "Paris"
        },
        {
          value: "Canberra",
          label: "Canberra"
        }
      ],
      tableHeader: [
        { name: "序" },
        { name: "项目" },
        { name: "姓名" },
        { name: "职务" },
        { name: "办公电话" },
        { name: "移动电话" },
